.pwa-install-banner {
  z-index: 10000;
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border-top: 3px solid #0f0;
  padding: 16px 20px;
  transition: bottom .3s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  bottom: -100px;
  left: 0;
  right: 0;
  box-shadow: 0 -4px 20px #00000080;
}

.pwa-install-banner-visible {
  bottom: 0;
}

.pwa-install-content {
  align-items: center;
  gap: 16px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.pwa-install-icon {
  flex-shrink: 0;
  font-size: 2rem;
}

.pwa-install-text {
  flex-direction: column;
  flex: 1;
  gap: 4px;
  display: flex;
}

.pwa-install-text strong {
  color: #0f0;
  font-size: 1rem;
}

.pwa-install-text span {
  color: #b0b0b0;
  font-size: .875rem;
}

.pwa-install-actions {
  flex-shrink: 0;
  gap: 8px;
  display: flex;
}

.pwa-update-banner {
  z-index: 10000;
  background: linear-gradient(135deg, #2a1e1e 0%, #3a2a2a 100%);
  border-bottom: 3px solid #fa0;
  padding: 16px 20px;
  transition: top .3s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: -100px;
  left: 0;
  right: 0;
  box-shadow: 0 4px 20px #00000080;
}

.pwa-update-banner-visible {
  top: 0;
}

.pwa-update-content {
  align-items: center;
  gap: 16px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.pwa-update-icon {
  flex-shrink: 0;
  font-size: 2rem;
  animation: 2s linear infinite rotate;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.pwa-update-text {
  flex-direction: column;
  flex: 1;
  gap: 4px;
  display: flex;
}

.pwa-update-text strong {
  color: #fa0;
  font-size: 1rem;
}

.pwa-update-text span {
  color: #b0b0b0;
  font-size: .875rem;
}

.pwa-update-actions {
  flex-shrink: 0;
  gap: 8px;
  display: flex;
}

.pwa-offline-indicator {
  color: #fff;
  z-index: 9999;
  background: #f44;
  border-radius: 0 0 12px 12px;
  padding: 12px 24px;
  transition: top .3s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: -60px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 4px 12px #f446;
}

.pwa-offline-indicator-visible {
  top: 0;
}

.pwa-offline-content {
  align-items: center;
  gap: 12px;
  display: flex;
}

.pwa-offline-icon {
  font-size: 1.5rem;
}

.pwa-offline-text {
  font-size: .938rem;
  font-weight: 600;
}

.pwa-toast {
  z-index: 10001;
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border-radius: 12px;
  max-width: 400px;
  padding: 16px 24px;
  transition: bottom .3s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  bottom: -100px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 4px 20px #00000080;
}

.pwa-toast-visible {
  bottom: 24px;
}

.pwa-toast-content {
  color: #fff;
  align-items: center;
  gap: 12px;
  display: flex;
}

.pwa-toast-icon {
  flex-shrink: 0;
  font-size: 1.5rem;
}

.pwa-toast-success {
  border-left: 4px solid #0f0;
}

.pwa-toast-info {
  border-left: 4px solid #0af;
}

.pwa-toast-warning {
  border-left: 4px solid #fa0;
}

.pwa-toast-error {
  border-left: 4px solid #f44;
}

.pwa-ios-instructions {
  z-index: 10000;
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border-top: 3px solid #007aff;
  padding: 20px;
  transition: bottom .3s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  bottom: -200px;
  left: 0;
  right: 0;
  box-shadow: 0 -4px 20px #00000080;
}

.pwa-ios-instructions-visible {
  bottom: 0;
}

.pwa-ios-content {
  max-width: 600px;
  margin: 0 auto;
}

.pwa-ios-header {
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
  display: flex;
}

.pwa-ios-icon {
  font-size: 1.5rem;
}

.pwa-ios-header strong {
  color: #007aff;
  flex: 1;
  font-size: 1rem;
}

.pwa-ios-close {
  color: #b0b0b0;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  padding: 0;
  font-size: 1.5rem;
  transition: background .2s;
  display: flex;
}

.pwa-ios-close:hover {
  background: #ffffff1a;
}

.pwa-ios-steps {
  color: #e0e0e0;
  font-size: .938rem;
  line-height: 1.5;
}

.pwa-ios-steps p {
  margin: 0;
}

.pwa-btn {
  cursor: pointer;
  white-space: nowrap;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: .938rem;
  font-weight: 600;
  transition: all .2s;
}

.pwa-btn-primary {
  color: #000;
  background: #0f0;
}

.pwa-btn-primary:hover {
  background: #0d0;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px #00ff004d;
}

.pwa-btn-secondary {
  color: #b0b0b0;
  background: none;
  border: 1px solid #444;
}

.pwa-btn-secondary:hover {
  color: #fff;
  background: #ffffff0d;
  border-color: #666;
}

.pwa-btn:active {
  transform: translateY(0);
}

@media (width <= 768px) {
  .pwa-install-banner, .pwa-update-banner {
    padding: 12px 16px;
  }

  .pwa-install-content, .pwa-update-content {
    gap: 12px;
  }

  .pwa-install-icon, .pwa-update-icon {
    font-size: 1.5rem;
  }

  .pwa-install-text strong, .pwa-update-text strong {
    font-size: .938rem;
  }

  .pwa-install-text span, .pwa-update-text span {
    font-size: .813rem;
  }

  .pwa-install-actions, .pwa-update-actions {
    flex-direction: column;
    gap: 6px;
  }

  .pwa-btn {
    width: 100%;
    padding: 8px 16px;
    font-size: .875rem;
  }

  .pwa-offline-indicator {
    border-radius: 0 0 8px 8px;
    left: 16px;
    right: 16px;
    transform: none;
  }

  .pwa-toast {
    max-width: none;
    left: 16px;
    right: 16px;
    transform: none;
  }
}

@media (width <= 480px) {
  .pwa-install-content, .pwa-update-content {
    flex-wrap: wrap;
  }

  .pwa-install-text, .pwa-update-text, .pwa-install-actions, .pwa-update-actions {
    width: 100%;
  }
}

.pwa-btn:focus {
  outline-offset: 2px;
  outline: 2px solid #0f0;
}

.pwa-ios-close:focus {
  outline-offset: 2px;
  outline: 2px solid #007aff;
}

@media (prefers-reduced-motion: reduce) {
  .pwa-install-banner, .pwa-update-banner, .pwa-offline-indicator, .pwa-toast, .pwa-ios-instructions {
    transition: none;
  }

  .pwa-update-icon {
    animation: none;
  }
}
